import React,{useState} from "react";
import Console from './comp/Console';
import './comp/Css.css'
function App() {
  const[inp,setInp]=useState("");
  const[clc,setClc]=useState([]);



  const change=(e)=>{
    setInp(e.target.value);
  }

  const click=()=>{
    setClc((old)=>{
      return [...old,inp]
    });
    setInp('');



  }



  return (
  <div className="back">
<Console/>

 <div className="flex justify-center back">
    <h1>ToDo-List</h1>
    </div>
    <div className="flex justify-center back">
    <input className="bb" value={inp} type="text" name="" id="" placeholder="add a item" onChange={change}/>
    <button className="btn" onClick={click}>+</button>
  </div>

    <ol>
      {clc.map((it)=>{
        return <li>{it}</li>
      })}
    </ol>







  </div>

  );
}

export default App;
